  input[type="text"],
  input[type="password"],
  textarea,
  select
  {
    height: 50px;
    padding: 30px 260px; 
    background-color: #fff;
    border: 2px solid #D1D1D1;
    padding-left: 15px;
    border-radius: 4px;
    box-shadow: none;
    box-sizing: border-box; 
  }
  
  input[type="text"]:focus,
  input[type="password"]:focus,
  select
